.Home {
  background-image: url("../Assets/multi.jpg");
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}

.enter {
  width: 100px;
  height: 50px;
  background-color: #cc9543;
  font-family: "Open Sans", sans-serif;
  font-weight: 900;
  border-radius: 0.2em;
  box-shadow: 0 0.05em 0.25em 0 rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border: none;
}

.enter:hover {
  opacity: 0.9;
}
